<template>
  <!-- {{ DetailList{0]} }} -->
  <div v-if="listItem && listItem.galContentTypeId == Number(listId)">
    <div class="max-w-7xl mx-auto border box-border p-4 rounded-md mt-7">
      <h2 class="font-bold mb-2 text-xl text-center">{{ listItem.galTitle }}</h2>
      <img :src="listItem.galWebImageUrl" :alt="listItem.galWebImageUrl">
      <ul>
        <li class="mb-5"><span class="font-extrabold">포토그래퍼</span> : {{ listItem.galPhotographer }}</li>
        <li class="mb-5"><span class="font-extrabold">장소</span> : {{ listItem.galPhotographyLocation }}</li>
        
        <li class="mb-5"><span class="font-extrabold">작성 날짜</span> : {{ listItem.galModifiedtime }}</li>
        <li class="mb-5"><span class="font-extrabold">수정 날짜</span> : {{ listItem.galCreatedtime }}</li>
        <li class="mb-5 leading-8"><span class="font-extrabold">검색 키워드</span> : {{ listItem.galSearchKeyword }}</li>
        <li class="mb-5"><button @click="open_page(HOMEPAGE_URL)"
          class="hover:font-extrabold mb-1 py-1 px-5  border rounded transition-all hover:text-white hover:shadow-[inset_-130px_0_0_0_lightblue,inset_130px_0_0_0_lightblue] duration-700">한국관광공사 홈페이지</button></li>
       
        <li><button @click="$router.go(-1)" class="text-[18px] hover:font-extrabold mb-1 py-1 px-5  border rounded transition-all hover:text-white hover:shadow-[inset_-60px_0_0_0_orange,inset_60px_0_0_0_orange] duration-700">뒤로가기</button></li>
      </ul>
      <div class="flex mt-5">
        <div id="map" class="w-2/4 h-96"></div>
        <div id="map2" class="w-2/4 h-96"></div>
      </div>
    </div>
  </div>
  <div v-else :click="$router.replace('/404')">자료가 없습니다.
    <!-- <button @click="$router.replace('/')" class="cursor-pointer">뒤로가기</button> -->
  </div>
</template>

<script>

// import axios from 'axios';
// 카카오 맵 JS키 : eacb6b17f74c881c607784fe11e8e44b
export default {
  
  data(){
    return{
      HOMEPAGE_URL: 'http://tong.visitkorea.or.kr',
      listItem:[],
      listId:this.$route.params.id,
      map: null,
      markers: [],
      latitude: 0,
      longitude: 0
    }
  },
  methods:{
    open_page(url){
      window.open(url);
    },
    initMap() {
      const container = document.getElementById("map");
      var roadviewContainer = document.getElementById("map2");
      var roadview = new kakao.maps.Roadview(roadviewContainer); //濡쒕뱶酉� 媛앹껜
      var roadviewClient = new kakao.maps.RoadviewClient(); //醫뚰몴濡쒕��� 濡쒕뱶酉� �뚮끂ID瑜� 媛��몄삱 濡쒕뱶酉� helper媛앹껜
      var position = new kakao.maps.LatLng(this.listItem.LAT, this.listItem.LNG); //吏��꾩쓽 以묒떖醫뚰몴.
      roadviewClient.getNearestPanoId(position, 50, function(panoId) {
    roadview.setPanoId(panoId, position); //panoId�� 以묒떖醫뚰몴瑜� �듯빐 濡쒕뱶酉� �ㅽ뻾
    }); 
      const options = {
        center: new kakao.maps.LatLng(this.listItem.LAT, this.listItem.LNG),
        level: 5,
      };
      this.map = new kakao.maps.Map(container, options);
      this.displayMarker([[this.listItem.LAT, this.listItem.LNG]]);
    },
    displayMarker(markerPositions) {
      if (this.markers.length > 0) {
        this.markers.forEach((marker) => marker.setMap(null));
      }

      const positions = markerPositions.map(
          (position) => new kakao.maps.LatLng(...position)
      );

      
      if (positions.length > 0) {
        this.markers = positions.map(
            (position) =>
                new kakao.maps.Marker({
                  map: this.map,
                  position,
                })
        );

        const bounds = positions.reduce(
            (bounds, latlng) => bounds.extend(latlng),
            new kakao.maps.LatLngBounds()
        );


        this.map.setBounds(bounds);
      }
    }
  },
  name:"DetailPage",
  created(){
    this.listItem = JSON.parse(sessionStorage.getItem('data'))
    console.log("테스트 문구 출력")
      
      if (!("geolocation" in navigator)) {
      return;
      }
    if (window.kakao && window.kakao.maps) {
      this.initMap();

      } else {
      const script = document.createElement("script");
      /* global kakao */
      script.onload = () => kakao.maps.load(this.initMap);
      // script.src = "//dapi.kakao.com/v2/maps/sdk.js?appkey=eacb6b17f74c881c607784fe11e8e44b"
      script.src = `//dapi.kakao.com/v2/maps/sdk.js?autoload=false&appkey=eacb6b17f74c881c607784fe11e8e44b`
      document.head.appendChild(script);
      }
    } 
  }   
    
  
  

</script>

<style>
 /* 원스토어 모바일 팝 */
 @font-face {
      font-family: 'ONE-Mobile-POP';
      src: url('https://cdn.jsdelivr.net/gh/projectnoonnu/noonfonts_2105_2@1.0/ONE-Mobile-POP.woff') format('woff');
      font-weight: normal;
      font-style: normal;
  }
  div, ul, p{ font-family: 'ONE-Mobile-POP'; }
</style>